import { Component, ViewChild, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { ProductService } from "../../providers/product.services";
import { ToastController, ModalController } from "@ionic/angular";
import { CheckOutOfferPage } from "./modal/checkOutOffer";
import { UserData } from "../../providers/user-data";
declare var paytm: any;

@Component({
  selector: "page-checkout",
  templateUrl: "checkout.html",
  styleUrls: ["./checkout.scss"],
})
export class CheckoutPage implements OnInit {
  public cartList: any = [];
  public topStories: any;
  public coupncode: "";
  public cartId: any;
  public orderTotal: any = 0;
  public addressId: any;
  public flashoffer = false;
  todayDate: any = new Date().toJSON().slice(0, 10);
  constructor(
    private _ProductService: ProductService,
    private _Router: ActivatedRoute,
    public Router: Router,
    private toastController: ToastController,
    public modalController: ModalController,
    private _UserData: UserData
  ) {}

  public searchproductList: any = [];
  ngOnInit() {
    this.cartId = this._Router.snapshot.params.cartId;
    this.addressId = this._Router.snapshot.params.addressId;
    console.log(this.addressId, "addressId");
    console.log(this.cartId, "cartId");
    this.getCartList();
    this.getCouponsList();
    this.getCustomerProfile();
    this.flash();
    // this.fetchOffers();
  }
  public aplicableofferDiscountArr = [];
  public rewarddiscount = 0;
  public isSearchEnable: Boolean = false;

  flash() {
    setInterval(() => {
      this.flashoffer = true;

      setTimeout(() => {
        this.flashoffer = false;
      }, 1000);
    }, 5000);
  }

  public itemValue:any =0;

  getCartList() {
    let shopId = localStorage.getItem("shopID");
    this._ProductService
      .cartList(localStorage.getItem("userId"), shopId)
      .subscribe((data: any) => {
        console.log(data, "cart List");

        this.cartList = data.length == 0 ? [] : data[0].productDetails;

        console.log("data['productDetails']", data[0]["productDetails"]);
        let total = 0;
        data[0]["productDetails"].forEach((val) => {
          if (val.productId) {
            let discount = val.productId.discount;
            let price = val.productId.price;
            let discountedPrice = (price * discount) / 100;
            total = total + (price - discountedPrice) * val.productCount;
            val["discountedPrice"] = (price - discountedPrice).toFixed(2);
            // alert("price : " + price + "discount : " + discount  )
            // alert("discounted Price : " + discountedPrice + "final : " + val["discountedPrice"]  )
          }
        });

        console.log(total);

        this.orderTotal = total.toFixed(2);
        this.itemValue = total.toFixed(2);

        this._ProductService.listOffer("").subscribe(async (res: any) => {
          console.log(res);
          if (res) {
            this._offerList = res["data"];
          }

          this._offerList.forEach((offer) => {
            if (
              offer.isRewardSetting &&
              offer.minAmount <= parseInt(this.orderTotal)
            ) {
              this.aplicableofferDiscountArr.push(offer.discountedAmount);
            }
          });

          this._offerList = this._offerList.filter(
            (offer) => parseInt(offer.minAmount) > parseInt(this.orderTotal)
          );
          this._offerList.sort(function (a, b) {
            return parseFloat(a.minAmount) - parseFloat(b.minAmount);
          });

          if (this.aplicableofferDiscountArr.length) {
            var largest = 0;

            for (let i = 0; i <= largest; i++) {
              if (this.aplicableofferDiscountArr[i] > largest) {
                largest = this.aplicableofferDiscountArr[i];
              }
            }
            console.log(
              largest,
              this.aplicableofferDiscountArr,
              "===================="
            );
            this.rewarddiscount = largest;
          }
        });
        this.getAddress();
      });
  }

  public addressData;
  getAddress() {
    this._ProductService
      .getAddressDetails(localStorage.getItem("userId"), this.addressId)
      .subscribe((data: any) => {
        console.log(data, "address Data");
        this.addressData = data;
      });
  }

  public _couponsList: any = [];
  getCouponsList() {
    let arr = [];
    this._ProductService.listOffer(false).subscribe(async (res: any) => {
      console.log(res);
      if (res) {
        this._couponsList = res["data"];
      }
    });
  }

  public offerDiscounAmount: any = 0.0;
  public offerApplied = false;
  public invalidCoupnError = false;
  public coupnAppliedMsg = false;

  public couponId = "";
  apply() {
    console.log(this._couponsList);
    this._couponsList.forEach(async (offer) => {
      if (this.coupncode.toLowerCase() == offer.couponCode.toLowerCase()) {
        this.offerDiscounAmount = offer.discountedAmount;
        this.offerDiscounAmount = this.offerDiscounAmount.toFixed(2);
        this.couponId = offer._id;

        if (this.orderTotal < this.offerDiscounAmount) {
          let toast = await this.toastController.create({
            message: "Applied Amount is invalid , Offer Can not Applied",
            color: "warning",
            duration: 2000,
          });
          toast.present();
          return;
        }

        this.invalidCoupnError = false;
        this.orderTotal = this.orderTotal - this.offerDiscounAmount;
        this.orderTotal = this.orderTotal.toFixed(2);
        this.offerApplied = true;
        this.coupncode = "";
        this.coupnAppliedMsg = true;
      } else {
        // alert("called")
        this.invalidCoupnError = true;
        setTimeout(() => {
          this.invalidCoupnError = false;
        }, 2000);
      }
    });
  }

  public filter = false;

  async openOfferModal() {
    const modal = await this.modalController.create({
      component: CheckOutOfferPage,
      componentProps: {
        filterData: this.filter,
      },
    });

    modal.onDidDismiss().then((dataReturned) => {
      if (dataReturned !== null) {
      }
    });

    return await modal.present();
  }

  public failureCallback(error) {
    console.log(error, "error++++++++++++++++++++");
    alert("Transaction Failed for reason " + error.RESPMSG);
    this.Router.navigateByUrl(`/orderSummary/null`);
  }

  public _offerList: any = [];
  fetchOffers() {
    let arr = [];
    this._ProductService.listOffer("").subscribe(async (res: any) => {
      console.log(res);
      if (res) {
        this._offerList = res["data"];
        this._offerList = this._offerList.filter(
          (offer) => parseInt(offer.minAmount) > parseInt(this.orderTotal)
        );
      }
    });
  }

  payment() {
    let self = this;
    // self.Router.navigateByUrl(`/orderSummary/12364`);
    this._ProductService
      .getCheckSum({
        mobile: "8920832260",
        email: "abc@gmail.com",
        customer_id: localStorage.getItem("userId"),
        amount: this.orderTotal,
      })
      .subscribe((data: any) => {
        console.log(data, "orderData");
        let checksum = data.checksum;
        let orderId = data.payload.ORDER_ID;
        // let options = {
        //   REQUEST_TYPE: "DEFAULT",
        //   ENVIRONMENT: "staging",
        //   MID: "Nrrwgu05043713596268", // You would get this details from paytm after opening an account with them
        //   ORDER_ID: orderId, // Unique ID for each transaction. This info is for you to track the transaction details
        //   CUST_ID: "1000098811100", // Unique ID for your customer
        //   INDUSTRY_TYPE_ID: "Retail", // You would get this details from paytm after opening an account with them
        //   CHANNEL_ID: "WAP", // You would get this details from paytm after opening an account with them
        //   TXN_AMOUNT: "1", // Transaction amount that has to be collected
        //   WEBSITE: "WEBSTAGING", // You would get this details from paytm after opening an account with them
        //   MOBILE_NO: "8920832260", // Mobile no of customer
        //   EMAIL: "abc@gmail.com", // Email of customer
        //   CALLBACK_URL: `https://securegw.paytm.in/theia/paytmCallback?ORDER_ID=${orderId}`, // Callback url
        //   CHECKSUMHASH: checksum,
        // };

        let options = data.payload;
        options["ENVIRONMENT"] = "staging";
        options["CHECKSUMHASH"] = checksum;

        console.log("==============>>>", options);

        paytm.startPayment(
          options,
          function (success) {
        let payload = {
          cartId: self.cartId,
          addressId: self.addressId,
          ownerId: self._ProductService.getOwnerId(),
          couponApplied: self.coupnAppliedMsg,
          rewardApplied: self.rewarddiscount ? true : false,
          coinsApplied: self.isEnoughBalance ? true : false,
          couponAmount: self.offerDiscounAmount,
          rewardAmount: self.rewarddiscount,
          coinAmount: self.isEnoughBalance ? 100 : 0,
          balance: self.userData.walletBalance + self.orderTotal - (self.rewarddiscount + self.offerDiscounAmount)

        };

        if (self.couponId) {
          payload["couponId"] = self.couponId;
        }
        console.log(JSON.stringify(success));
        self._ProductService.orderProduct(payload).subscribe((data: any) => {
          console.log(data, "orderData");
          if (success.STATUS == "TXN_SUCCESS") {
            let orderId = data[0].saveDataInOrderTable.orderId;
          self.Router.navigateByUrl(`/orderSummary/${orderId}`);
          } else {
          alert("Transaction Failed for reason " + success.RESPMSG);
          }
          });
        }, this.failureCallback.bind(this));
      });
  }

  async cartAction(action, productData, saveForLater = "") {
    let count = 0;
    if (action == "add") {
      count = 1;
    } else if (action == "remove") {
      count = -1;
    }

    if (saveForLater == "saveForLater") {
      console.log("productData", productData);
      count = -productData.productCount;
    }

    let payload = {
      productDetails: {
        productId: productData.productId._id,
        productCount: count,
      },
      UserId: localStorage.getItem("userId"),
      shopId: localStorage.getItem("shopID"),
    };
    this._ProductService
      .addToCart(action, payload)
      .subscribe(async (data: any) => {
        if (data) {
          this.getCartList();
        }
      });
  }

  public userData = {
    walletBalance: 0,
    loyaltyCardNumber: "",
  };
  getCustomerProfile() {
    this._UserData
      .getCustomerDetails(localStorage.getItem("userId"))
      .subscribe((data: any) => {
        this.userData = data;
        // this.userData.walletBalance = 1000;
        console.log(this.userData, "---");
      });
  }

  public isEnoughBalance = false;
  async claimCoins() {
    if (this.userData.walletBalance >= 100) {
      this.isEnoughBalance = true;
      this.orderTotal = this.orderTotal - 100
    } else {
      this.isEnoughBalance = false;
      const toast = await this.toastController.create({
        message: "You don`t have sufficent coins to claim. Min 100 coins are required",
        duration: 3000,
        color: "warning",
        position: "bottom",
        animated: true,
      });
      toast.present();
      return;
    }
  }
}
