import { Component, ViewChild, OnInit, Input } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { ProductService } from "../../../providers/product.services";
import { Clipboard } from '@ionic-native/clipboard/ngx';
import { ToastController, ModalController } from '@ionic/angular';


@Component({
  selector: "page-checkOutOffer",
  templateUrl: "checkOutOffer.html",
  styleUrls: ["./checkOutOffer.scss"],
})
export class CheckOutOfferPage implements OnInit {



  public productList: any;
  public searchWord: any;
  public filter = '';
  @Input() filterData

  constructor(private _ProductService: ProductService,private clipboard: Clipboard,
    public toastController: ToastController,
    public modalController: ModalController,
    private _Router: ActivatedRoute) {}

  public searchproductList: any = [];
  ngOnInit() {
    console.log(this.filterData,"filterData")
    this.filter = this.filterData



    this.getProductList();
    this.fetchOffers();
    this.getCashbacks();

  }

  closeModel(){
    this.modalController.dismiss()
  }

  public reedemCount=0
  getCashbacks(){
    this._ProductService.getCashBacks(localStorage.getItem('userId')).subscribe(async (res: any) => {
      console.log(res);
      if (res) {
        let list = res

        list.forEach((val)=>{
          this.reedemCount = this.reedemCount + val.amount ;
        })
      }
    });
  }

  onFilterChange(){
  console.log(this.filter);
  this.fetchOffers()
  }

  public _offerList: any = [];
  public cartCount = 0;


  fetchOffers() {
    this.getCartCount()
    this._ProductService.listOffer(this.filterData).subscribe(async (res: any) => {
      console.log(res);
      if (res) {
        this._offerList = res["data"];
      }
    });
  }

  async copy(code){
    console.log(code ,":: code ")
    this.clipboard.clear()
    this.clipboard.copy(code);
    const toast = await this.toastController.create({
      message: "Offer Code Copied",
      duration: 2000,
      position: "bottom",
      color:'success',
      animated: true,
    });
    toast.present();

    // this.toast.
  }


  public showSearchBox: Boolean = false;
  enableSearch() {
    this.showSearchBox = true;
  }

  backToNormal() {
    this.showSearchBox = false;
  }

  getProductList() {
    this._ProductService.getProductList(localStorage.getItem('shopID')).subscribe((data: any) => {
      this.productList = data;
      console.log(this.productList);
    });
  }

  getCartCount(){
    let shopId = localStorage.getItem('shopID')
    this._ProductService
      .cartList(localStorage.getItem("userId"),shopId)
      .subscribe((data: any) => {
        if(data.length){
          this.cartCount = data[0].productDetails.length;
        }
      })
  }

  async openSocial(network: string, fab: HTMLIonFabElement) {
    // const loading = await this.loadingCtrl.create({
    //   message: `Posting to ${network}`,
    //   duration: (Math.random() * 1000) + 500
    // });
    // await loading.present();
    // await loading.onWillDismiss();
    // fab.close();
  }
}
